.service-message{
    font-family: "Noto Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight:600;
    font-size: 45px;
    font-style: normal;
    text-shadow: 0 0 3px #FF0000, 0 0 10px black;
    color:orangered;
}

.Services-body{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 1px solid black;
    background-color:lightgrey;
}

.pic-sec-body{
    background-image: url('images/cleanyard.jpeg');
    background-size: contain;
    background-repeat: none;
    background-position: center;
    background-size: 100%;
    width: 100vw;
    height: 60vh;
    color: rgba(255,255,255,1);
    display: flex;
    align-items: center;
    justify-content: center;
}

.inner-service-body{
    margin-top:10vh;
    display:grid;
    gap: 50px;
    grid-template-columns: auto auto auto;
}



.overview-sec{
    width:100%;
    height:35vh;
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    font-family: "Noto Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight:400;
    font-style: normal;
}

.overview-sec > h5 {
    font-size: large;
    margin-top: 75px;
    margin-bottom: 10vh;
}

.the-info-link{
    text-decoration: none;
    color:black;
}

.site-title{
    text-align: center;
}

.site-description{
    padding-left: 2vw;
    padding-right: 2vw;
}