.book-here-body{
    margin-top: 10vh;
    margin-bottom: 10vh;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: center;
    width: 40vw;
    height:85vh;
    border: 1px solid black;
    border-radius: 15px;
    background-color: rgba(0,0,0,0.05);
    overflow-y: scroll;
    font-family: "Noto Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight:400;
    font-style: normal;
    
    /* padding-bottom: 200px; */

}

.book-here-title{
    text-shadow: 0 0 10px orange, 0 , 0, 5px red;
    align-self: center;
}

.booking-input-body{
    width: 80%;
    height: 55px;
    border-radius:40px;
    box-sizing: border-box;
    margin: 20px;
}

.booking-input-body > input{
    display: flex;
    flex:1;
    width:100%;
    height:100%;
    background-color: transparent;
    border-radius: 40px;
    padding-left: 10px;
    
}
.booking-input-body > input:hover{
    background-color: lightgray;
}
.book-here-body > form{
    box-sizing: content-box;
    width:100%;
}

.submit-booking{
    background-color:rgba(0,0,0,0.05);
    align-self:center;
    margin-left: 20px;
    width:400px;
    border-radius: 25px;
}

.submit-booking:hover{
    background-color: lightgray;
}

   
.YS-body{
    /* background-color: blue; */
    width:80%;
    border-radius:15px;
    margin-left: 15px;
    padding-top: 1rem;
}

.ys-title{
    text-align: center;
}

.YS-grid-cont{
    margin-top: 10px;
    margin-bottom: 10px;
    display: grid;
    place-items: center;
    grid-template-columns: auto auto auto;
    row-gap: 2px;
    border-top: 0.5px solid black;
    border-bottom: 0.5px solid black;

}

.test_active{
    background-color:blue;
}

.test_active:active{
    background-color:red;  
}

.YS-grid-cont{
    display:flex;
    flex-direction: column;
    align-items: flex-start;
}

.Submit-order-box{
    display: none;
}