.TD-body{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin-top: 10px;
    color: black;
}

.TD-body > h1 {
    margin-top:20px;
    color: black;
    margin-bottom: 10px;

    font-family: "Noto Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight:425;
    font-style: normal;
    font-size: 35px;
}

.TD-body > h1:hover {
    color: black;
}

.slide-show-body{
    margin-top: 20px;
    width: 80%;
    height:100px;
    border: 1px dotted blue;
    display: flex;
    justify-content: space-evenly;
}

.slide-show-body > img {
    border: 5px solid black;
    width: 150px;
}

